.loading {
  background-image: -webkit-gradient(linear, left top, left bottom, from(0), color-stop(#e1e1e1), color-stop(50%, #e1e1e1), color-stop(50%, transparent));
  background-image: -webkit-linear-gradient(0, #e1e1e1, #e1e1e1 50%, transparent 50%);
  background-image: -o-linear-gradient(0, #e1e1e1, #e1e1e1 50%, transparent 50%);
  background-image: linear-gradient(0, #e1e1e1, #e1e1e1 50%, transparent 50%);
  background-position: 100% 100%;
  background-size: 100% 1px;
  background-repeat: no-repeat;
  text-align: center;
  height: 45px;
  color: #666;
  font-size: 14px;
}

.loading__img {
  vertical-align: middle;
  margin-right: 5px;
  width: 20px;
  height: 20px;
  display: inline-block;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: -1px 0;
  background-size: contain;
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
  -webkit-animation-name: loading;
          animation-name: loading;
  -webkit-animation-duration: 1.2s;
          animation-duration: 1.2s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(0);
  }
  50% {
    -webkit-transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes loading {
  0% {
    -webkit-transform: rotate(0);
  }
  50% {
    -webkit-transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}



